.links-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    /* gap: 20px; */
    margin-top: 20px;
  }
  
  .link {
    text-decoration: none;
    font-weight: 500;
    color: black;
    /* background-color: white; */
    transition: all 0.3s ease;
    padding: 5px;
  }

  .link:hover {
    text-decoration: underline;
  }

